@charset "UTF-8";

/*basic styles*/

* {
  box-sizing: border-box;
  transition: .5s;
}

html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  width: 100%;
  font-family: 'Lato', sans-serif;
  font-size: 14px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: #fff;
}

.wrapper {
  position: relative;
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

.section {
  height: 100%;
  width: 100%;
  min-height: 650px;
}

.container {
  width: 960px;
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
}

.title {
  font-size: 30px;
  font-weight: 900;
  color: #666d89;
}

.title--sub {
  font-size: 18px;
}

.title--grey {
  color: #f0d8d9;
}

.description {
  font-size: 18px;
  font-weight: 300;
  color: #767e9e;
  line-height: 30px;
}

.description--sub {
  font-weight: 400;
  font-size: 12px;
  line-height: 20px;
}

.description--grey {
  color: #f0d8d9;
}

/*base end*/

/************************************/

/*intro section*/

.intro {
  height: 100%;
  width: 100%;
  background-color: #eb5055;
}

/***********************************/

/*header*/

.header {
  padding: 30px 0;
  background-color: #303441;
}

.header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/*header end*/

/*logo block*/

.logo__link {
  display: inline-block;
  font-size: 25px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;
}

/**********************************/

/*main block*/

.main {
  height: 100%;
  width: 100%;
}

.main__wrapper {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main__block {
  text-align: center;
  color: #fff;
}

.main__block .title {
  color: #fff;
  font-size: 80px;
}

.main__block-title {
  margin-bottom: 50px;
}

.main__block-subtitle {
  font-size: 40px;
  font-weight: 600;
}

/**********************************/

/*menu block*/

.menu__list {
  display: flex;
}

.menu__item {
  margin-right: 25px;
}

.menu__item:last-child {
  margin-right: 0;
}

.menu__link {
  font-weight: 700;
  font-size: 13px;
  text-transform: uppercase;
}

.menu__link:hover {
  color: red;
}

/**********************************/

/*services section*/

.services {
  padding: 50px 0;
  background-color: #f2f2f2;
}

/**********************************/

/*content block*/

.content__title {
  margin-bottom: 25px;
}

.content__description {
  margin-bottom: 45px;
}

.content__list {
  display: flex;
  flex-wrap: wrap;
}

.content__item {
  width: 31%;
  margin-right: 20px;
  margin-bottom: 25px;
  display: flex;
  align-items: flex-start;
}

.content__item:nth-child(3n) {
  margin-right: 0;
}

.content__item:nth-child(4) {
  margin-bottom: 0;
}

.content__item:nth-child(5) {
  margin-bottom: 0;
}

.content__item:last-child {
  margin-right: 0;
  margin-bottom: 0;
}

.content__item .content__description {
  margin-bottom: 0;
}

.content__icon {
  display: block;
  height: 60px;
  width: 60px;
  margin-right: 20px;
  border: 2px solid #666d89;
  border-radius: 50%;
  flex-shrink: 0;
  position: relative;
}

.content__icon-pic {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/**********************************/

/*portfolio section*/

.portfolio {
  background-color: #303441;
  padding: 50px 0;
}

.portfolio__content {
  margin-bottom: 15px;
}

.portfolio__option {
  margin-bottom: 15px;
}

.portfolio__gallery-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: -20px;
}

.portfolio__gallery-item {
  height: 190px;
  max-width: 23%;
  margin-bottom: 20px;
}

.portfolio__gallery-image {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

/**********************************/

/*about section*/

.about {
  padding: 60px 0;
  background-color: #f2f2f2;
}

.about__content {
  margin-bottom: 45px;
}

.about__content .content__description {
  margin-bottom: 0;
}

/**********************************/

/*option block*/

.option__list {
  display: flex;
}

.option__item {
  margin-right: 15px;
}

.option__item:last-child {
  margin-right: 0;
}

.option__item--active .option__link {
  color: #fff;
  border-color: #fff;
}

.option__link {
  display: inline-block;
  padding: 5px 10px;
  border: 1px solid #767e9e;
  border-radius: 10px;
  font-weight: 400;
  font-size: 12px;
  color: #767e9e;
}

.option__link:hover {
  color: #fff;
  border-color: #fff;
}

/**********************************/

/*socials block*/

.socials__list {
  display: flex;
}

.socials__item {
  margin-right: 10px;
}

.socials__item:last-child {
  margin-right: 0;
}

.socials__link {
  display: inline-block;
  width: 32px;
  height: 32px;
  border: 1px solid #747b94;
  border-radius: 50%;
  position: relative;
  /*модификаторы для разных иконок*/
  /******/
}

.socials__link:hover {
  background-color: #d2d2c8;
  border-color: blue;
}

.socials__link:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  height: 15px;
  width: 15px;
  display: inline-block;
  transform: translate(-50%, -50%);
  background: url(../img/socials/facebook.png) center center no-repeat;
}

/**********************************/

/*employee block*/

.employee__list {
  display: flex;
}

.employee__item {
  margin-right: 25px;
}

.employee__item:last-child {
  margin-right: 0;
}

.employee__avatar {
  margin-bottom: 20px;
}

.employee__avatar-icon {
  max-width: 100%;
  max-height: 100%;
}

.employee__title {
  margin-bottom: 20px;
}

.employee__description {
  margin-bottom: 30px;
}

/********************************/

/*contact section*/

.contact {
  padding: 50px 0;
  background-color: #eb5055;
  color: #f0d8d9;
}

.contact__content {
  margin-bottom: 45px;
}

.contact__content .content__description {
  margin-bottom: 0;
}

.contact__block {
  display: flex;
  justify-content: space-between;
}

.contact__form {
  width: 70%;
  margin-right: 20px;
}

.contact__information {
  flex: 1;
}

.contact__information-content {
  margin-bottom: 30px;
}

.contact__information-content .content__description {
  margin-bottom: 0;
}

/********************************/

/*adress block*/

.adress__item {
  margin-bottom: 10px;
}

.adress__item:last-child {
  margin-bottom: 0;
}

.adress__link {
  display: inline-block;
  font-weight: 400;
  font-size: 12px;
  color: #f0d8d9;
}

/********************************/

/*form block*/

.form__label {
  width: 100%;
  height: 100%;
  display: inline-block;
  margin-bottom: 15px;
}

.form__label:last-child {
  margin-bottom: 0;
}

.form__input {
  width: 100%;
  padding: 15px 0 15px 10px;
  outline: none;
  border: 1px solid transparent;
  border-radius: 5px;
  background-color: #fff;
  color: #f48c8f;
  font-size: 12px;
  font-weight: 400;
}

.form__textarea {
  height: 200px;
  resize: none;
}

/*******************************/

/*burger block*/

.burger {
  display: none;
  width: 25px;
  height: 25px;
  cursor: pointer;
  position: fixed;
  top: 30px;
  right: 25px;
  z-index: 10;
}

.burger .line {
  display: inline-block;
  background-color: #fff;
  width: 100%;
  height: 3px;
  transition: .5s;
  border-radius: 5px;
  position: absolute;
}

.burger .line:nth-child(1) {
  top: 0;
  left: 0;
}

.burger .line:nth-child(2) {
  top: 50%;
  transform: translateY(-50%);
}

.burger .line:nth-child(3) {
  bottom: 0;
  left: 0;
}

.lines .line {
  transform-origin: 50% 50%;
}

.lines .line:nth-child(1) {
  display: none;
}

.lines .line:nth-child(2) {
  top: 46%;
  left: 0;
  transform: rotate(220deg);
}

.lines .line:nth-child(3) {
  bottom: 50%;
  transform: translateY(50%) rotate(-45deg);
}

/*******************************/

/*fullmenuscreen section*/

.fullmenuscreen {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0;
  visibility: hidden;
  z-index: 5;
}

.fullmenuscreen__menu {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.fullmenuscreen .menu {
  display: block;
}

.fullmenuscreen .menu__list {
  flex-direction: column;
}

.fullmenuscreen .menu__item {
  width: 100%;
  margin-right: 0;
  margin-bottom: 40px;
}

.fullmenuscreen .menu__item:last-child {
  margin-bottom: 0;
}

.fullmenuscreen .menu__link {
  color: #fff;
  font-size: 45px;
}

.fullmenuscreen .menu__link:hover {
  color: red;
}

/*******************************/

/*popup section*/

.popup {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  background-color: black;
  visibility: hidden;
  z-index: 11;
}

.popup__wrapper {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup__block {
  color: #fff;
}

.popup__title {
  font-size: 20px;
  font-weight: 600;
}

.popup__clickname {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}

/******************************/

.is-active {
  opacity: 1;
  visibility: visible;
}

@media screen and (max-width: 992px) {
  .container {
    padding: 0 5%;
  }
}

@media screen and (max-width: 768px) {
  .header .container {
    justify-content: center;
  }

  .main__block-subtitle {
    font-size: 35px;
  }

  .menu {
    display: none;
  }

  .content__item {
    width: 48%;
  }

  .content__item:nth-child(2n) {
    margin-right: 0;
  }

  .content__item:nth-child(3) {
    margin-right: 25px;
  }

  .portfolio__gallery-item {
    max-width: 31%;
  }

  .burger {
    display: inline-block;
  }
}

@media screen and (max-width: 480px) {
  .main__block .title {
    font-size: 65px;
  }

  .content__item {
    width: 100%;
    margin-right: 0;
    margin-bottom: 25px;
  }

  .content__item:nth-child(3) {
    margin-right: 0;
    margin-bottom: 25px;
  }

  .content__item:nth-child(4) {
    margin-right: 0;
    margin-bottom: 25px;
  }

  .content__item:nth-child(5) {
    margin-bottom: 25px;
  }

  .portfolio__gallery-item {
    max-width: 48%;
  }

  .employee__list {
    flex-direction: column;
  }

  .employee__item {
    width: 100%;
    margin-right: 0;
    margin-bottom: 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .employee__item:last-child {
    margin-bottom: 0;
  }

  .contact__block {
    flex-direction: column;
  }

  .contact__form {
    width: 100%;
    margin-right: 0;
    margin-bottom: 25px;
  }

  .contact__information {
    text-align: center;
  }
}