/*basic styles*/
    *{
        box-sizing: border-box;
        transition: .5s;
    }

    html, body{
        padding: 0;
        margin: 0;
        height: 100%;
        width: 100%;
        font-family: 'Lato', sans-serif;
        font-size: 14px;

    }

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
    }


    a{
        text-decoration: none;
        color: #fff;
    }

    .wrapper{
        position: relative;
        height: 100%;
        width: 100%;
        overflow-x: hidden;
        scroll-behavior: smooth;
    }

    .section{
        height: 100%;
        width: 100%;
        min-height: 650px;
    }

    .container{
        width: 960px;
        max-width: 100%;
        height: 100%;
        margin: 0 auto;

        @include smallmonitor{
            padding: 0 5%;
        }
    }

    .title{
        font-size: 30px;
        font-weight: 900;
        color: #666d89;

        &--sub{
            font-size: 18px;
        }

        &--grey{
            color: #f0d8d9;
        }
    }

    .description{
        font-size: 18px;
        font-weight: 300;
        color: #767e9e;
        line-height: 30px;

        &--sub{
            font-weight: 400;
            font-size: 12px;
            line-height: 20px;
        }

        &--grey{
            color: #f0d8d9;
        }
    }

/*base end*/

/************************************/

/*intro section*/

.intro{
    height: 100%;
    width: 100%;
    background-color: #eb5055;
}

/***********************************/

/*header*/

.header{
    padding: 30px 0;
    background-color: #303441;

    .container{
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include tablets{
            justify-content: center;
        }
    }
}
/*header end*/

/*logo block*/

.logo{

    &__link{
        display: inline-block;
        font-size: 25px;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 2px;
    }
}

/**********************************/

/*main block*/

.main{
    height: 100%;
    width: 100%;

    &__wrapper{
        height: 100%;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__block{
        text-align: center;
        color: #fff;

        .title{
            color: #fff;
            font-size: 80px;

            @include phones{
                font-size: 65px;
            }
        }

        &-title{
            margin-bottom: 50px;
        }

        &-subtitle{
            font-size: 40px;
            font-weight: 600;

            @include tablets{
                font-size: 35px;
            }
        }
    }
}
/**********************************/

/*menu block*/

.menu{

    &__list{
        display: flex;
    }

    &__item{
        margin-right: 25px;

        &:last-child{
            margin-right:0;
        }
    }

    &__link{
        font-weight: 700;
        font-size: 13px;
        text-transform: uppercase;

        &:hover{
            color:red;
        }
    }

    @include tablets{
        display: none;
    }
}

/**********************************/

/*services section*/

.services{
    padding: 50px 0;
    background-color: #f2f2f2;
}

/**********************************/

/*content block*/

.content{

    &__title{
        margin-bottom: 25px;
    }

    &__description{
        margin-bottom: 45px;
    }

    &__list{
        display: flex;
        flex-wrap: wrap;
    }

    &__item{
        width: 31%;
        margin-right: 20px;
        margin-bottom: 25px;

        display: flex;
        align-items: flex-start;

        &:nth-child(3n){
            margin-right:0;
        }

        &:nth-child(4){
            margin-bottom:0;
        }

        &:nth-child(5){
            margin-bottom:0;
        }

        &:last-child{
            margin-right:0;
            margin-bottom:0;
        }

        .content{
            &__description{
                margin-bottom: 0;
            }
        }

        @include tablets{
            width: 48%;

            &:nth-child(2n){
                margin-right: 0;
            }

            &:nth-child(3){
                margin-right: 25px;
            }
        }

        @include phones{
            width: 100%;
            margin-right: 0;
            margin-bottom: 25px;
            
            &:nth-child(3){
                margin-right: 0;
                margin-bottom: 25px;
            }

            &:nth-child(4){
                margin-right: 0;
                margin-bottom: 25px;
            }

            &:nth-child(5){
                margin-bottom:25px;
            }
        }
    }

    &__icon{
        display: block;
        height: 60px;
        width: 60px;
        margin-right: 20px;
        border: 2px solid #666d89;
        border-radius: 50%;
        flex-shrink: 0;
        position: relative;

        &-pic{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

}
/**********************************/

/*portfolio section*/

.portfolio{
    background-color: #303441;
    padding: 50px 0;

    &__content{
        margin-bottom: 15px;
    }

    &__option{
        margin-bottom: 15px;
    }

    &__gallery{
        
        &-list{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            margin-bottom: -20px;
        }

        &-item{
            height: 190px;
            max-width: 23%;
            margin-bottom: 20px;

            @include tablets{
                max-width: 31%;
            }

            @include phones{
                max-width: 48%;
            }

        }

        &-image{
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }
}
/**********************************/

/*about section*/

.about{
    padding: 60px 0;
    background-color: #f2f2f2;

    &__content{
        margin-bottom: 45px;

        .content__description{
            margin-bottom: 0;
        }
    }
}

/**********************************/

/*option block*/

.option{

    &__list{
        display: flex;
    }

    &__item{
        margin-right: 15px;

        &:last-child{
            margin-right:0;
        }

        &--active{
            
            .option__link{
                color: #fff;
                border-color: #fff;
            }
        }
    }

    &__link{
        display: inline-block;
        padding: 5px 10px;
        border:1px solid #767e9e;
        border-radius: 10px;
        font-weight: 400;
        font-size: 12px;
        color: #767e9e;

        &:hover{
            color: #fff;
            border-color: #fff;
        }
    }
}
/**********************************/


/*socials block*/

.socials{
    
    &__list{
        display: flex;
    }

    &__item{
        margin-right: 10px;

        &:last-child{
            margin-right:0;
        }
    }

    &__link{
        display: inline-block;
        width: 32px;
        height: 32px;
        border: 1px solid #747b94;
        border-radius: 50%;
        position: relative;

        &:hover{
            background-color:#d2d2c8;
            border-color: blue;
        }

        &:before{
            content:'';
            position: absolute;
            top: 50%;
            left: 50%;
            height: 15px;
            width: 15px;
            display: inline-block;
            transform: translate(-50%, -50%);
            background: url(../img/socials/facebook.png) center center no-repeat;
        }
        
        /*модификаторы для разных иконок*/

        /******/
    }
}
/**********************************/

/*employee block*/

.employee{

    &__list{
        display: flex;

        @include phones{
            flex-direction: column;
        }
    }

    &__item{
        margin-right: 25px;

        &:last-child{
            margin-right:0;
        }

        @include phones{
            width: 100%;
            margin-right: 0;
            margin-bottom: 25px;

            display: flex;
            flex-direction: column;
            align-items: center;

            &:last-child{
                margin-bottom:0;
            }
        }
    }

    &__avatar{
        margin-bottom: 20px;

        &-icon{
            max-width: 100%;
            max-height: 100%;
        }
    }

    &__title{
        margin-bottom: 20px;
    }

    &__description{
        margin-bottom: 30px;
    }
}
/********************************/

/*contact section*/

.contact{
    padding: 50px 0;
    background-color: #eb5055;
    color: #f0d8d9;

    &__content{
        margin-bottom: 45px;

        .content{

            &__description{
                margin-bottom: 0;
            }
        }
    }

    &__block{
        display: flex;
        justify-content: space-between;

        @include phones{
            flex-direction: column;
        }
    }

    &__form{
        width: 70%;
        margin-right: 20px;

        @include phones{
            width: 100%;
            margin-right: 0;
            margin-bottom: 25px;
        }
    }

    &__information{
        flex:1;

        @include phones{
            text-align: center;
        }

        &-content{
            margin-bottom: 30px;

            .content{
                &__description{
                    margin-bottom: 0;
                }
            }
        }
    }

}
/********************************/

/*adress block*/

.adress{
    &__list{}

    &__item{
        margin-bottom: 10px;

        &:last-child{
            margin-bottom:0;
        }
    }

    &__link{
        display: inline-block;
        font-weight: 400;
        font-size: 12px;
        color: #f0d8d9;
    }
}
/********************************/

/*form block*/

.form{
    
    &__label{
        width: 100%;
        height: 100%;
        display: inline-block;
        margin-bottom: 15px;

        &:last-child{
            margin-bottom:0;
        }
    }

    &__input{
        width: 100%;
        padding: 15px 0 15px 10px;
        outline: none;
        border:1px solid transparent;
        border-radius: 5px;

        background-color: #fff;
        color: #f48c8f;
        font-size: 12px;
        font-weight: 400;
    }

    &__textarea{
        height: 200px;
        resize: none;
    }
}

/*******************************/

/*burger block*/

.burger{
    display: none;
    width: 25px;
    height: 25px;
    cursor: pointer;

    position: fixed;
    top: 30px;
    right: 25px;
    z-index: 10;

    .line{
        display: inline-block;
        background-color: #fff;
        width: 100%;
        height: 3px;
        transition: .5s;
        border-radius: 5px;

        position: absolute;

        &:nth-child(1){
            top: 0;
            left: 0;
        }

        &:nth-child(2){
            top: 50%;
            transform: translateY(-50%);
        }

        &:nth-child(3){
            bottom: 0;
            left: 0;
        }
    }

    @include tablets{
        display: inline-block;
    }
}

.lines{

    .line{
        transform-origin: 50% 50%;

        &:nth-child(1){
            display: none;
        }

        &:nth-child(2){
            top: 46%;
            left: 0;
            transform: rotate(220deg);
        }

        &:nth-child(3){
            bottom: 50%;
            transform: translateY(50%) rotate(-45deg);
        }
    }
    
}

/*******************************/

/*fullmenuscreen section*/


.fullmenuscreen{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0;
    visibility: hidden;
    z-index: 5;

    &__menu{
        width: 100%;
        height: 100%;

        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .menu{

        display: block;

        &__list{
            flex-direction: column;
        }

        &__item{
            width: 100%;
            margin-right: 0;
            margin-bottom: 40px;

            &:last-child{
                margin-bottom:0;
            }
        }

        &__link{
            color: #fff;
            font-size: 45px;

            &:hover{
                color:red;
            }
        }
    }
}


/*******************************/

/*popup section*/

.popup{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    background-color: black;
    visibility: hidden;
    z-index: 11;

    &__wrapper{
        height: 100%;
        width: 100%;

        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__block{
        color: #fff;
    }

    &__title{
        font-size: 20px;
        font-weight: 600;
    }

    &__clickname{
        position: absolute;
        bottom: 20px;
        left: 50%;
        transform: translateX(-50%);
    }
}

/******************************/

.is-active{
    opacity: 1;
    visibility: visible;
}